/* Header */

header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* Socials */

.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.socials {
    font-size: 2em;
}

.header_socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* Me */
.me {
    background: linear-gradient(var(--primary-color), transparent);
    /* width: 22rem; */
    /* height: 30rem; */
    position: absolute;
    /* left: calc(50% - 11rem); */
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    /* border-radius: 12rem 12rem 0 0; */
    /* border-radius: 50%; */
    overflow: hidden;
}
.me img {
    border-radius: 12rem 12rem 0 0;
    width: 22rem;
    height: 22rem;
}
/* Scroll down */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header_socials,
    .scroll__down {
        display: none;
    }
}
